* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  list-style: none;
}

a {
  color: white;
}

a:link,a:visited,a:hover,a:active {
  text-decoration: none;
}

.footer a {
  color: #54585a;
}/* HEADER */.header {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: rgba(9, 157, 142, 0.9);
  color: white;
  font-size: 15px;
  line-height: 30px;
  font-family: rubik;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1;
  opacity: 0.9;
  box-sizing: border-box;
  font-weight: 500px;
}

.header__team__name {
  margin: 15px;
  text-transform: capitalize;
}

.header__team__name__yes {
  text-transform: uppercase;
}

.header__menu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: rgba(20, 217, 197, 0.9);
  width: 100%;
}

.header__menu__options {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.nav__team,.nav__who,.nav__contact {
  color: white;
  box-sizing: border-box;
  padding: 10px;
  text-decoration: none;
  font-weight: 500px;
  text-transform: uppercase;
}

.nav__who {
  min-width: 140px;
}/* HERO */.hero {
  background-image: url(../images/hero_768px.jpg);
  background-size: cover;
  background-position: left;
  width: 100%;
  height: 550px;
  bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.hero__claim {
  font-family: OpenSans-Semibold;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  background-color: white;
  width: 80%;
  transform: translateY(50%);
  padding: 10px;
  margin: 0px;
  font-weight: 600px;
}/*  TEAM */.team {
  width: 100%;
  margin-top: 40px;
  padding: 30px 15px 30px 15px;
}

.team__h2 {
  font-family: "Rubik-Medium", sans-serif;
  font-size: 24px;
  text-align: center;
  color: #099d8d;
  height: 45px;
  align-self: center;
  text-transform: uppercase;
}

.team__description {
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  line-height: 24px;
  color: #54585a;
  align-self: center;
}

.anchor__helper {
  display: block;
  position: relative;
  top: -120px;
  visibility: hidden;
}/* DECORATION */.decoration {
  height: 30px;
  width: 30px;
  background: white;
  transform: rotate(45deg) translate(30%, 30%);
}

.decoration__container {
  width: 100%;
  display: flex;
  justify-content: center;
}/* QUALITIES S&F */.qualities__container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #e8e2e1;
  width: 100%;
  padding: 0px 15px 45px 15px;
}

.qualities__title {
  font-family: "Rubik-Medium", sans-serif;
  font-size: 22px;
  font-weight: 500px;
  text-align: center;
  align-self: center;
  color: #099d8d;
  height: 45px;
  margin-top: 30px;
}

.qualities__description {
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  line-height: 24px;
  color: #54585a;
}/* ABOUT US */.individual__description {
  padding-top: 30px;
}

.individual__description__title {
  font-family: "Rubik-Medium", sans-serif;
  color: #099d8d;
  line-height: 45px;
  text-align: center;
  text-transform: uppercase;
}

.individual__title {
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  line-height: 45px;
  margin: 0;
}

.anchor__who {
  display: block;
  position: relative;
  top: -110px;
  visibility: hidden;
}

.adalabers {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.individual__image__adalaber {
  max-height: 180px;
  box-sizing: border-box;
  border: solid 5px;
  border-color: rgba(0, 0, 0, 0.08);
  margin: 0;
}

.individual__bio {
  font-family: "Open Sans", sans-serif;
  line-height: 24px;
  color: #54585a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0 30px;
}

.individual__text {
  text-align: center;
}

.logos {
  color: #099d8d;
  box-sizing: border-box;
  padding: 5px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.fab,.fas {
  color: #099d8d;
  margin-right: 10px;
}/* CONTACT */.contact__hero {
  background-image: url(../images/background__contact_320.jpg);
  background-size: cover;
  width: 100%;
  height: 300px;
}

.contact__info__title {
  border: red solid;
  font-size: 24px;
  text-transform: uppercase;
  text-align: center;
  color: #099d8d;
  line-height: 45px;
  margin: 30px auto 15px;
}

contact__info {
  padding: 15px 0px 15px 0px;
}

.contact__info__text {
  text-align: center;
}

contact__info--small {
  text-align: center;
}

.form .label {
  display: block;
  padding: 10px;
}/* FOOTER  */.footer {
  padding: 30px 0 45px 0;
  display: flex;
  border: 1px solid #b8b8b9;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}

.footer__copyright {
  font-family: "Open Sans", sans-serif;
  color: #b8b8b9;
  line-height: 20px;
  text-align: center;
}

.footer__menu {
  font-family: "Open Sans", sans-serif;
  text-align: center;
  line-height: 20px;
  color: #54585a;
  font-weight: 600;
  opacity: 0.75;
  padding: 20px 0px 30px 0px;
  text-transform: uppercase;
}

@media all and (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 30px 0px 30px;
    align-items: baseline;
  }

  .header__team__name {
    min-width: 35%;
  }

  .header__menu {
    background-color: transparent;
    width: 50%;
  }

  .header__menu__options {
    justify-content: flex-end;
  }

  .hero {
    background-image: url(../images/hero_1200px.jpg);
    align-items: flex-start;
  }

  .hero__claim {
    transform: translateY(0%);
    margin-left: 5%;
    max-width: 500px;
    opacity: 0.8;
  }

  .anchor__helper {
    display: block;
    position: relative;
    top: -80px;
    visibility: hidden;
  }

  .team {
    margin-top: 0px;
    padding: 45px 45px 30px 45px;
  }

  .qualities__container {
    flex-direction: row;
    justify-content: space-between;
    padding: 45px 45px 60px 45px;
  }

  .qualities--strengths,.qualities--weaknesses {
    width: 50%;
    padding: 0px 15px;
  }

  .qualities__title {
    margin-top: 0px;
  }

  .qualities__description {
    text-align: center;
  }

  .adalabers {
    flex-direction: row;
    min-width: 400px;
  }

  .individual__bio {
    align-items: flex-start;
  }

  .individual__text {
    text-align: left;
  }

  .adalabers:nth-child(even) {
    flex-direction: row-reverse;
    align-items: center;
    min-width: 400px;
  }

  .adalabers:nth-child(even) .individual__bio {
    align-items: flex-end;
  }

  .adalabers:nth-child(even) .individual__bio .individual__text {
    text-align: right;
  }

  .anchor__who {
    top: -70px;
  }

  .footer {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    padding: 24px 45px 6px 45px;
  }

  .footer__copyright,.footer__menu,.footer__logo {
    width: 50%;
    padding: 0px;
  }

  .footer__copyright {
    padding-top: 6px;
  }

  .footer__menu__list {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    line-height: 30px;
    margin: 0px;
  }

  .footer__copyright {
    text-align: left;
  }

  .footer__logo {
    position: relative;
    bottom: 55px;
    width: 116px;
  }
}

@media all and (min-width: 1024px) {
  .individual__description {
    padding: 30px 138px 0 138px;
  }
}

@media all and (min-width: 1200px) {
  .team {
    padding: 45px 0px 30px 0px;
  }

  .team__description {
    max-width: 601px;
    margin: 0 auto;
  }

  .qualities__container {
    padding: 45px 138px 60px 138px;
  }

  .qualities--strengths,.qualities--weaknesses {
    padding: 0px 23px;
  }

  .footer {
    padding: 24px 138px 6px 138px;
  }
}
